import { Component } from '@angular/core';
import {IonicPage, ModalController, NavController, NavParams, ToastController} from 'ionic-angular';
import {TaskmodalPage} from "../taskmodal/taskmodal";
import {TaskviewmodalPage} from "../taskviewmodal/taskviewmodal";

import { TaskServiceProvider } from '../../providers/task-service/task-service';

@IonicPage()
@Component({
  selector: 'page-all-items',
  templateUrl: 'all-items.html',
})
export class AllItemsPage {
  private tasks: Array<any>;
  constructor(public navCtrl: NavController,public navparams:NavParams, public modalCtrl: ModalController, public taskServiceProvider: TaskServiceProvider, public toastCtrl: ToastController) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AllItemsPage');
    this.taskServiceProvider.getMyTasks().subscribe(tasks=>{
      this.tasks=tasks;
    })
  }

  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');
      refresher.complete();
    }, 2000);
  }

  openModal(Taskid){
    let myModal = this.modalCtrl.create(TaskmodalPage,Taskid);
    myModal.present();
    myModal.onDidDismiss(() => this.ionViewDidLoad())
  }

  openviewModal(tid){
    let myModal = this.modalCtrl.create(TaskviewmodalPage,tid);
    myModal.present();
    myModal.onDidDismiss(() => this.ionViewDidLoad())
  }

  remove(task) {
    this.taskServiceProvider.remove(task.id).subscribe(response => {
      for (let i = 0; i < this.tasks.length; i++) {
        if (this.tasks[i] === task) {
          this.tasks.splice(i, 1);
          let toast = this.toastCtrl.create({
            message: 'Task "' + task.title + '" deleted.',
            duration: 2000,
            position: 'top'
          });
          toast.present();
        }
      }
    });
  }
}



