import {Component, ViewChild} from '@angular/core';
import {NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {TaskServiceProvider} from "../../providers/task-service/task-service";
import {NgForm} from "@angular/forms";


@Component({
  selector: 'page-taskmodal',
  templateUrl: 'taskmodal.html',
})
export class TaskmodalPage {
  @ViewChild('title') title;
  task: any = {};
  error: any;

  constructor(public taskServiceProvider: TaskServiceProvider,
              public params: NavParams,
              public viewCtrl: ViewController,
              public toastCtrl: ToastController,
              public navCtrl: NavController) {
    if (this.params.data.id) {
      this.taskServiceProvider.get(this.params.get('id')).subscribe((task: any) => {
        this.task = task;
        this.task.href = task._links.self.href;
      });
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TaskmodalPage');
    setTimeout(() => {
      this.title.setFocus();
    }, 150);
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

  save(form: NgForm) {
    let update: boolean = form['href'];
    this.taskServiceProvider.save(form).subscribe(result => {
      let toast = this.toastCtrl.create({
        message: 'Task "' + form.name+ '" ' + ((update) ? 'updated' : 'added') + '.',
        duration: 2000
      });
      toast.present();
      this.closeModal();
    }, error => this.error = error)
  }
}



