import {Component, ViewChild} from '@angular/core';
import {IonicPage, NavController, NavParams, ToastController, ViewController} from 'ionic-angular';
import {TaskServiceProvider} from "../../providers/task-service/task-service";

/**
 * Generated class for the TaskviewmodalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-taskviewmodal',
  templateUrl: 'taskviewmodal.html',
})
export class TaskviewmodalPage {
  @ViewChild('title') title;
  task: any = {};
  constructor(public navCtrl: NavController, public params: NavParams, public viewCtrl: ViewController,
              public toastCtrl: ToastController,public taskServiceProvider: TaskServiceProvider) {
    if (this.params.data.id) {
      this.taskServiceProvider.get(this.params.get('id')).subscribe((task: any) => {
        this.task = task;
        this.task.href = task._links.self;
      });
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TaskviewmodalPage');

  }

  closeModal() {
    this.viewCtrl.dismiss();
  }

}



